import React from 'react';
import './index.scss';

const ContactBox = ({ icon, cta, contact, description, subdescription }) => {
  return (
    <div class='contactbox--wrapper'>
      <div class='contactbox--wrapper-cta'>
        <img src={icon} alt='cta' />
        <p>{cta}</p>
      </div>
      <div class='contactbox--contact'>
        <p>{contact}</p>
      </div>
      <div class='contactbox--description'>
        <p>{description}</p>
        {subdescription ? <p>{subdescription}</p> : <span/>}
      </div>
    </div>
  );
};

export default ContactBox;
